import React, { useEffect } from 'react';

import { ChatBubble, Circle, Delete, Map } from '@mui/icons-material';
import {
  Box,
  Button,
  Stack,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography,
  styled,
  IconButton,
} from '@mui/material';
import { useParams, Link as RouterLink, useNavigate } from 'react-router-dom';
import { map } from 'rxjs/operators';

import { useObservable } from '../hooks/useObservable';
import { ManagementService } from '../services/ManagementService';
import { FeatureContainer } from './FeatureContainer';
import { FirstAction } from './FirstAction';
import { Flex } from './Flex';

export type ContentScreenProps = {
  managementService: ManagementService;
};

const ContentTableRow = styled(TableRow)(({ theme }) => ({
  '&:hover': {
    backgroundColor: theme.palette.grey['50'],
    cursor: 'pointer',
  },
}));

const ContentTableBaseCell = styled(TableCell)(({ theme }) => ({
  padding: theme.spacing(1),
}));

const ContentTableStatusCell = styled(ContentTableBaseCell)(({ theme }) => ({
  paddingLeft: theme.spacing(2),
  paddingRight: theme.spacing(2),
  width: 0,
}));
const ContentTableNameCell = styled(ContentTableBaseCell)({ width: '50%' });
const ContentTableSummaryCell = styled(ContentTableBaseCell)({
  maxWidth: 350,
});
const ContentTableActionsCell = styled(ContentTableBaseCell)(({ theme }) => ({
  paddingLeft: theme.spacing(2),
  paddingRight: theme.spacing(2),
  width: 0,
}));

const StepCount: React.FC<{ count: number }> = ({ count }) => (
  <Box sx={{ display: 'inline-flex', opacity: 0.8, position: 'relative' }}>
    <ChatBubble />
    <Typography
      sx={{
        color: '#fff',
        fontSize: '0.7rem',
        position: 'absolute',
        textAlign: 'center',
        top: '2px',
        width: '100%',
      }}
    >
      {count}
    </Typography>
  </Box>
);

export const ContentScreen: React.FC<ContentScreenProps> = ({
  managementService,
}) => {
  const { siteId } = useParams<{ siteId: string }>();
  const navigate = useNavigate();

  const [site] = useObservable(
    () =>
      managementService.sites$.pipe(map(ss => ss?.find(s => s.id === siteId))),
    [managementService],
  );
  const [tours] = useObservable(
    () => managementService.tours$,
    [managementService],
  );

  useEffect(() => {
    if (siteId !== undefined) {
      void managementService.loadTours(siteId);
    }
  }, [managementService, siteId]);

  if (site === undefined || tours === undefined) {
    return null;
  }

  if (tours.length === 0) {
    return (
      <FeatureContainer>
        <FirstAction
          actionsComponent={
            <Stack direction="row" spacing={2}>
              <Button
                onClick={() => undefined}
                size="large"
                variant="contained"
              >
                Create content
              </Button>
            </Stack>
          }
          illustrationComponent={<Map />}
        >
          Create your first custom content experience.
        </FirstAction>
      </FeatureContainer>
    );
  }

  return (
    <Box mx="auto" width="100%">
      <Flex my={4}>
        <Button
          color="primary"
          component={RouterLink}
          to="new"
          variant="contained"
        >
          Create tour
        </Button>
      </Flex>
      <Table width="100%">
        <TableHead>
          <TableRow>
            <ContentTableStatusCell />
            <ContentTableNameCell>Name</ContentTableNameCell>
            <ContentTableSummaryCell>Summary</ContentTableSummaryCell>
            <ContentTableActionsCell />
          </TableRow>
        </TableHead>
        <TableBody>
          {tours.map(tour => {
            return (
              <ContentTableRow
                key={tour.id}
                onClick={() => navigate(tour.id, { relative: 'path' })}
              >
                <ContentTableStatusCell>
                  <Circle
                    color={tour.status === 'published' ? 'success' : 'disabled'}
                    sx={{
                      height: '0.6rem',
                      width: '0.6rem',
                    }}
                    titleAccess={tour.status}
                  />
                </ContentTableStatusCell>
                <ContentTableNameCell>{tour.name}</ContentTableNameCell>
                <ContentTableSummaryCell>
                  <Flex flexWrap="nowrap" sx={{ opacity: 0.875 }}>
                    <StepCount count={tour.steps.length} />
                    <Typography
                      sx={theme => ({
                        display: 'inline',
                        marginLeft: theme.spacing(1),
                        overflow: 'hidden',
                        textOverflow: 'ellipsis',
                        whiteSpace: 'nowrap',
                      })}
                      variant="body2"
                    >
                      {getTextFromHtml(tour.steps.map(s => s.html).join(' | '))}
                    </Typography>
                  </Flex>
                </ContentTableSummaryCell>
                <ContentTableActionsCell>
                  <IconButton
                    onClick={ev => {
                      ev.stopPropagation();
                      void managementService.deleteTour(site.id, tour.id);
                    }}
                    size="small"
                  >
                    <Delete />
                  </IconButton>
                </ContentTableActionsCell>
              </ContentTableRow>
            );
          })}
        </TableBody>
      </Table>
    </Box>
  );
};

function getTextFromHtml(html: string): string {
  try {
    const text =
      new DOMParser()
        .parseFromString(html, 'text/html')
        ?.body.textContent?.trim() ?? '';
    return text === '' ? 'No content' : text;
  } catch (err) {
    return 'No content';
  }
}
