import React, { useEffect, useMemo, useState } from 'react';

import type { Site } from '@enactive-ac/api/src/domain/Site';
import { Palette } from '@mui/icons-material';
import BubbleChartIcon from '@mui/icons-material/BubbleChart';
import {
  AppBar,
  Box,
  Button,
  CircularProgress,
  Container,
  IconButton,
  styled,
  Toolbar,
  Typography,
  useTheme,
} from '@mui/material';
import {
  Link as RouterLink,
  Navigate,
  Route,
  Routes,
  useMatch,
} from 'react-router-dom';

import { useObservable } from '../hooks/useObservable';
import { ManagementService } from '../services/ManagementService';
import { AccountDropdown } from './AccountDropdown';
import { ContentEditScreen } from './ContentEditScreen';
import { ContentScreen } from './ContentScreen';
import { EditThemeDrawer } from './EditThemeDrawer';
import { InstallationScreen } from './InstallationScreen';
import { SignInScreen } from './SignInScreen';

const BodyContainer = styled(Container)({
  alignItems: 'center',
  display: 'flex',
  flexDirection: 'column',
  flexGrow: 1,
  width: '100%',
});

const MenuContent = styled(Box)({
  '& > a': {
    border: 'solid 0.3em rgba(0, 0, 0, 0)',
    borderLeftWidth: 0,
    borderRadius: 0,
    borderRightWidth: 0,
    height: '100%',
    padding: '0 2em',
  },
  '& > a.active': {
    borderBottomColor: 'currentColor',
  },
  alignItems: 'center',
  display: 'flex',
  flexGrow: 1,
  height: '100%',
});

const MenuLogo = styled(BubbleChartIcon)(({ theme }) => ({
  height: theme.typography.h5.fontSize,
  marginRight: theme.spacing(1),
  width: theme.typography.h5.fontSize,
}));

const MenuTitle = styled(Typography)(({ theme }) => ({
  marginRight: theme.spacing(4),
}));

const Root = styled(Box)({
  display: 'flex',
  flexDirection: 'column',
  height: '100vh',
});

const RootFullScreen = styled(Root)({
  alignItems: 'center',
  justifyContent: 'center',
  width: '100%',
});

const managementService = new ManagementService();

// Add the enactive script so we have enactive integration functionality on our own site
const enactiveScript = document.createElement('script');
enactiveScript.src = `https://${
  window.location.hostname
}/integration/entrypoint.js?_=${Date.now()}`;
enactiveScript.setAttribute(
  'enactive.site-id',
  '8c60a02a-3b36-4cf1-874a-bbf0d68f6e29',
);
enactiveScript.setAttribute('enactive.enabled', 'false');
document.body.appendChild(enactiveScript);

export const AppRoot: React.FC = () => {
  const theme = useTheme();
  const [me] = useObservable(() => managementService.me$, []);
  const [sites] = useObservable(() => managementService.sites$, []);
  const [site, setSite] = useState<Site>();

  const { viewId } =
    useMatch({ end: false, path: '/sites/:siteId/:viewId' })?.params ?? {};

  const [waitingForSignIn, setWaitingForSignIn] = useState(false);
  const signedIn = useMemo(() => {
    if (
      me !== null &&
      me !== undefined &&
      site !== undefined &&
      !waitingForSignIn
    ) {
      return true;
    } else if (me !== undefined) {
      return false;
    } else {
      return undefined;
    }
  }, [me, site, waitingForSignIn]);

  const [openEditThemeDrawer, setOpenEditThemeDrawer] = useState(false);

  useEffect(() => {
    void managementService.init();
  }, []);

  useEffect(() => {
    if (me !== undefined) {
      if (me === null) {
        setWaitingForSignIn(true);
        enactiveScript.removeAttribute('enactive.context.user.id');
      } else {
        enactiveScript.setAttribute('enactive.context.user.id', me.id);
      }

      // Don't enable the integration until we have resolved whether or not we are signed in right
      // now, and only enable it if we are running on production host
      if (
        window.location.hostname.indexOf('enactive.ac') !== -1 &&
        window.location.hostname.indexOf('local.enactive.ac') === -1
      ) {
        enactiveScript.setAttribute('enactive.enabled', 'true');
      }
    } else {
      enactiveScript.setAttribute('enactive.enabled', 'false');
    }
  }, [me]);

  useEffect(() => {
    if (sites !== undefined) {
      setSite(sites[0]);
    } else {
      setSite(undefined);
    }
  }, [sites]);

  return (
    <>
      {signedIn !== undefined ? (
        <Root>
          <AppBar position="static">
            <Toolbar>
              <MenuLogo />
              <MenuTitle variant="h6">Enactive Management</MenuTitle>
              <MenuContent>
                {signedIn && site !== undefined ? (
                  <>
                    <Button
                      className={viewId === 'content' ? 'active' : undefined}
                      color="inherit"
                      component={RouterLink}
                      to={`/sites/${site.id}/content`}
                    >
                      Content
                    </Button>
                    <Button
                      className={
                        viewId === 'installation' ? 'active' : undefined
                      }
                      color="inherit"
                      component={RouterLink}
                      to={`/sites/${site.id}/installation`}
                    >
                      Installation
                    </Button>
                  </>
                ) : undefined}
              </MenuContent>
              {(() => {
                if (!signedIn) {
                  return (
                    <Button
                      color="inherit"
                      component={RouterLink}
                      sx={{
                        display: 'flex',
                        height: '100%',
                        paddingX: theme.spacing(2),
                      }}
                      to="/signin"
                    >
                      Sign In
                    </Button>
                  );
                } else if (me !== null && me !== undefined) {
                  return (
                    <>
                      <Box mr={2}>
                        <IconButton
                          color="inherit"
                          onClick={() => setOpenEditThemeDrawer(true)}
                          size="large"
                          title="Edit theme"
                        >
                          <Palette />
                        </IconButton>
                      </Box>
                      <AccountDropdown
                        buttonProps={{
                          sx: { display: 'flex', height: '100%' },
                        }}
                        me={me}
                        onSignOut={() => managementService.logout()}
                      />
                    </>
                  );
                }
              })()}
            </Toolbar>
          </AppBar>
          <Routes>
            <Route
              element={
                !signedIn ? (
                  <BodyContainer>
                    <SignInScreen
                      managementService={managementService}
                      onSignIn={() => setWaitingForSignIn(false)}
                    />
                  </BodyContainer>
                ) : (
                  <Navigate replace to="/" />
                )
              }
              path="/signin"
            />
            <Route
              element={
                !signedIn || site === undefined ? (
                  <Navigate replace to="/signin" />
                ) : (
                  <>
                    <Routes>
                      <Route
                        element={
                          <Navigate replace to={`/sites/${site.id}/content`} />
                        }
                        path="/"
                      />
                      <Route
                        element={
                          <BodyContainer>
                            <ContentScreen
                              managementService={managementService}
                            />
                          </BodyContainer>
                        }
                        path="/sites/:siteId/content"
                      />
                      <Route
                        element={
                          <ContentEditScreen
                            managementService={managementService}
                          />
                        }
                        path="/sites/:siteId/content/:contentId"
                      />
                      <Route
                        element={
                          <BodyContainer>
                            <InstallationScreen />
                          </BodyContainer>
                        }
                        path="/sites/:siteId/installation"
                      />
                    </Routes>
                    <EditThemeDrawer
                      managementService={managementService}
                      onClose={() => setOpenEditThemeDrawer(false)}
                      onExited={() => undefined}
                      onSave={() => setOpenEditThemeDrawer(false)}
                      open={openEditThemeDrawer}
                      site={site}
                    />
                  </>
                )
              }
              path="/*"
            />
          </Routes>
        </Root>
      ) : (
        <RootFullScreen>
          <CircularProgress />
        </RootFullScreen>
      )}
    </>
  );
};
